body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  overflow: hidden;

}
.header{
  padding:10px;
  background-color:firebrick;
  color:#fff;
  position:fixed;
  top:0;
  right:0;
  left:0;
  z-index: 100;


}
.header h1{
  font-size:25px;
  padding:0;
  margin:0;
  text-align:center;
}
.map{
  width:100vw;
  height:100vh;
  position:relative;
}
.location-icon{
  font-size: 1.3rem;
  color: rgb(255, 81, 0);
}
.loader{
  display:flex;
  flex-direction:column;
  align-items:center;
  justify-content: center;
  min-height: 100vh;

}
.loader img{
  width: 400px;
}
.loader h1{
  margin: top -100px;
}
.location-info{
  position:absolute;
  bottom: 50px;
  left: 50px;
  width: 300px;
  min-height:150px;
  padding:20px;
  background-color: rgba(0,0,0,0.6);
  border-radius: 10px;
  font-size:18px;
  color:#fff;

  
}
.location-info ul{
  list-style-type:none;
  padding:0;

}
.location-info li{
  padding:5px 0;
}